<template>
  <div class="box">
    <!-- 标题模块 -->
    <NavBar>
      <template #go>
        <van-icon name="arrow-left" />
      </template>
      <template #title>
        商品列表
      </template>
    </NavBar>
    <!-- 搜索模块 -->
    <div class="search">
      <div class="search_hd">
        <van-icon name="search" />
        <input type="text" :value="$route.query.goodsName || '搜索商品'" @click="$router.push('/searchview')">
      </div>
      <div class="iconClass">
        <van-icon name="apps-o" />
      </div>
      <div class="app">
        <!-- 选择模块 -->
        <div class="choose">
          <span @click="diffrentAction(0)">综合</span><span @click="diffrentAction(1)">销量</span><span @click="diffrentAction(2)">价格</span>
        </div>
        <!-- 商品展示模块 -->
        <div class="shoppingList" v-if="goodsList[0]">
          <GoodsItem v-for="item in goodsList" :key="item.goods_id" :goods="item"></GoodsItem>
        </div>
        <div v-else class="write">
          <span>亲，暂无物品，搜索手机之类的吧</span>
      </div>
      </div>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/goods.vue'
import search from '@/api/product.js'
export default {
  name: 'searchlistIndex',
  data () {
    return {
      goodsList: [],
      // 配置对象
      settingObj: {
        sortType: undefined,
        sortPrice: undefined,
        categoryId: 0,
        goodsName: undefined,
        page: 1
      },
      bool: true,
      bool2: 0
    }
  },
  components: {
    GoodsItem
  },
  methods: {
    async getAction (obj) {
      const { data: { list: { data } } } = await search.getSearchGoods(obj)
      this.goodsList = data
    },
    diffrentAction (num) {
      switch (num) {
        case 0:
          this.settingObj.sortType = 'all'
          this.getAction(this.settingObj)
          break
        case 1:
          this.settingObj.sortType = 'sales'
          this.getAction(this.settingObj)
          break
        case 2:
          this.settingObj.sortType = 'price'
          this.settingObj.sortPrice = this.bool2
          this.getAction(this.settingObj)
      }
      this.bool2 = !this.bool ? 1 : 0
    }
  },
  created () {
    this.settingObj.goodsName = this.$route.query.goodsName
    this.getAction(this.settingObj)
  }
}
</script>

<style lang="less" scoped>
.write {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 70px 0 0;
  img {
    width: 200px;
  }
  font-size: 16px;
  color: gray;
}
.bgcColor{
  background-color: #fafafa;
}
.app {
  position: fixed;
  top: 124px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
}
// 搜索模块
.search {
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  display: flex;
  padding: 20px 12px;
  background-color: #fff;
  .search_hd {
    margin-right: 10px;
    display: flex;
    align-items: center;
    flex: 1;
    position: relative;
    i {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 8px;
      font-size: 20px;
      color: gray;
    }
    input {
      width: 100%;
      height: 34px;
      padding-left: 31px;
      background: #f6f8fa;
      outline: none;
      border: 0;
      border-radius: 17px;
      font-size: 16px;
    }
  }
  .iconClass {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 25px;
  }
}
// 选择模块
.choose {
  font-size: 16px;
  display: flex;
  padding-bottom: 20px;
  justify-content: space-around;
  background-color: #fff;
  span {
    &:active {
      color: red;
    }
  }
}
</style>
